//Admin style
.meta-container {
  display: block;
  margin-top: 20px;
  &__inside {
    text-align: right;
    width: 49%;
    display: inline-block;
  }
}
.meta-label {
  display: inline-block;
  font-weight: bold;
  margin-bottom: 5px;
  width: 49%;
  text-align: left;
}
div.ui-toggle {
  margin: 0;
  padding: 0;
  display: inline-block;
  label {
    transition: all 200ms ease;
    display: inline-block;
    position: relative;
    user-select: none;
    background: #8c8c8c;
    box-shadow: inset 0 0 0 0 #012231;
    border: 2px solid #8c8c8c;
    border-radius: 22px;
    width: 40px;
    height: 20px;
    div {
      transition: all 200ms ease;
      background: #fff;
      width: 20px;
      height: 20px;
      border-radius: 10px;
    }
  }
}
div.ui-toggle input[type="checkbox"] {
  display: none;
}
div.ui-toggle input[type="checkbox"]:checked + label {
  border-color: #012231;
  background: #012231;
  box-shadow: inset 0 0 0 10px #012231;
}
div.ui-toggle label:hover,
div.ui-toggle label > div:hover {
  cursor: pointer;
}
div.ui-toggle input[type="checkbox"]:checked + label > div {
  margin-left: 20px;
}
